:last-child {\r\n                margin-top: -2.5px;\r\n                transform: rotate(45deg);\r\n\r\n            }\r\n        }\r\n    }\r\n\r\n}\r\n\r\nfooter {\r\n    input.theme_choose {\r\n        width: $base-font-size * 3;\r\n        height: $base-font-size * 3;\r\n        border: 1px white solid;\r\n    }\r\n\r\n    .add_item {\r\n        width: $base-font-size * 3;\r\n        height: $base-font-size * 3;\r\n        border: 1px white solid;\r\n        background-color: #ff8686;\r\n    }\r\n}\r\n\r\nsection {\r\n    margin: $base-font-size * 1 0px 0px 0px;\r\n}\r\n\r\nhr {\r\n    border-top: 2px solid rgba(0, 0, 0, .5);\r\n}\r\n\r\n@for $i from 1 through 10 {\r\n    .width_#{$i}0 {\r\n        width: $i * 10%;\r\n    }\r\n\r\n    .width_#{$i}rem {\r\n        width: $i * 1rem;\r\n    }\r\n}\r\n\r\n\r\n\r\n.outer_page {\r\n    flex: 1;\r\n\r\n    .btn_add_list {\r\n        width: $base-font-size * 3;\r\n        height: $base-font-size * 3;\r\n        border: 1px solid $border-line-color;\r\n    }\r\n\r\n    .todo_wrapper {\r\n        // background-color: #da3536;\r\n        display: flex;\r\n        flex-wrap: nowrap;\r\n        overflow-x: auto;\r\n\r\n        .to_do_card {\r\n            min-width: $base-font-size * 9;\r\n            max-width: $base-font-size * 9;\r\n            // overflow-y: scroll;\r\n            height: $base-font-size * 18;\r\n            border-radius: 10px;\r\n            padding: $base-font-size * 1.5 $base-font-size * 1;\r\n            font-size: $base-font-size * .8;\r\n            margin-right: $base-font-size;\r\n            display: flex;\r\n            flex-direction: column;\r\n\r\n            hr {\r\n                border: 1px white solid;\r\n                margin-top: .5rem;\r\n                margin-bottom: .5rem;\r\n            }\r\n\r\n            .to_do_title {\r\n                font-size: 1rem;\r\n                font-weight: bold;\r\n                border-bottom: 1px white solid;\r\n                padding-bottom: .5rem;\r\n            }\r\n\r\n            .task_wrapper {\r\n                margin-top: .5rem;\r\n                overflow-y: auto;\r\n                flex: 1;\r\n\r\n                .to_do_item {\r\n                    &.done {\r\n                        text-decoration: line-through;\r\n                        color: $border-line-color;\r\n                    }\r\n                }\r\n            }\r\n        }\r\n\r\n        .to_do_card:first-child {\r\n            margin-left: $base-font-size * 2;\r\n        }\r\n    }\r\n}\r\n\r\n.inner_page {\r\n    flex: 1;\r\n    margin: 0px $base-font-size * 3;\r\n\r\n    .title_wrapper {\r\n\r\n        @include inner_template;\r\n\r\n        .ring {\r\n            @include ring;\r\n        }\r\n\r\n        .mask {\r\n            @include ring;\r\n            border: 5px solid transparent;\r\n        }\r\n    }\r\n\r\n    .tasklist {\r\n        max-height: 55vh;\r\n        overflow-y: auto;\r\n    }\r\n\r\n    .task {\r\n        @include inner_template;\r\n        align-items: center;\r\n        // justify-items: center;\r\n\r\n        .icon_pending,\r\n        .icon_done {\r\n            margin-right: $base-font-size * 3;\r\n            justify-self: end;\r\n            color: $border-line-color;\r\n        }\r\n\r\n        .done_date {\r\n            color: $border-line-color;\r\n            font-weight: 500;\r\n        }\r\n\r\n        .not_done {\r\n            opacity: 0%;\r\n        }\r\n\r\n        &.done {\r\n            .taskname {\r\n                color: red;\r\n                text-decoration: line-through;\r\n            }\r\n        }\r\n\r\n        &:first-child {\r\n            margin-top: 0px !important;\r\n        }\r\n    }\r\n}\r\n\r\n.line-in-flex {\r\n    height: 2px;\r\n    background-color: $border-line-color;\r\n    flex: 1;\r\n}\r\n\r\n.title {\r\n    font-size: $base-font-size * 2.5;\r\n}\r\n\r\n.border-color {\r\n    color: $border-line-color;\r\n}\r\n\r\n// color \r\n.bg_purple {\r\n    bac