te;\n    color: white;\n    font-size: $base-font-size;\n    transform: rotate(45deg);\n    justify-self: end;\n    margin-right: $base-font-size * 2.5;\n    margin-top: $base-font-size;\n    // transition: transform 1s linear;\n}"],"sourceRoot":""}]);
